#ditty-adminbar {
  flex: 0 0 auto;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  padding: 20px;
  background: #fff;
  border-bottom: 2px solid #f0f0f1;
  box-sizing: border-box;
}
#ditty-adminbar * {
  box-sizing: border-box;
}
#ditty-adminbar h2 {
  margin: 0;
}
#ditty-adminbar__contents {
  flex: 1;
  overflow: hidden;
}
#ditty-adminbar__logo {
  color: #19bf7c;
}
#ditty-adminbar__title {
  display: flex;
  align-items: center;
  gap: 10px;
}
#ditty-adminbar__title__element {
  font-size: 16px;
  line-height: 1.5em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#ditty-adminbar__description {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 13px;
  color: #808080;
}
#ditty-adminbar .ditty-field {
  width: 100%;
}
#ditty-adminbar .ditty-field textarea {
  display: block;
  font-size: 13px;
  line-height: 16px;
}
#ditty-adminbar button.ditty-has-updates {
  color: #fff;
  background: #19bf7c;
}

.ditty-adminbar__status {
  display: block;
  font-size: 12px;
  line-height: 12px;
  height: 18px;
  font-weight: normal;
  background: #ccc;
  padding: 2px 5px;
  border-radius: 3px;
  cursor: pointer;
}
.ditty-adminbar__status--publish {
  color: #fff;
  background: #19bf7c;
}

#ditty-editor__footerbar {
  flex: 0 0 auto;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  border-top: 2px solid #f0f0f1;
  box-sizing: border-box;
}
#ditty-editor__footerbar * {
  box-sizing: border-box;
}
#ditty-editor__footerbar h2 {
  margin: 0;
}

.ditty-tabs {
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  height: 40px;
  font-size: 14px;
  background: #e8e9eb;
}

.ditty-tab {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  padding: 0 15px;
  text-align: center;
  text-decoration: none;
  color: #616970;
  background: #f2f3f5;
  border-radius: 0;
  border: none;
  cursor: pointer;
  overflow: hidden;
  transition: color 0.25s ease, background-color 0.25s ease;
}
.ditty-tab:hover:not(.ditty-tab--active) {
  color: #fff;
  background-color: #19bf7c;
}
.ditty-tab--active {
  color: #19bf7c;
  background-color: #fff;
}
.ditty-tab__icon {
  flex: 0 0 auto;
  display: block;
  font-size: 18px;
}
.ditty-tab__icon svg {
  fill: currentColor;
}
.ditty-tab__label {
  flex: 1;
}

.ditty-tabs--primary {
  height: 60px;
  background: #1d2327;
}
.ditty-tabs--primary .ditty-tab {
  width: 62px;
  color: rgba(255, 255, 255, 0.8);
  background: #1d2327;
}
.ditty-tabs--primary .ditty-tab__label {
  display: none;
}
.ditty-tabs--primary .ditty-tab:hover:not(.ditty-tab--active) {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
}
.ditty-tabs--primary .ditty-tab--active {
  width: auto;
  color: #fff;
  background-color: #19bf7c;
  padding-left: 30px;
  padding-right: 30px;
}
.ditty-tabs--primary .ditty-tab--active .ditty-tab__label {
  display: block;
}
.ditty-tabs--primary .ditty-tab--active .ditty-tab__icon {
  display: none;
}

.ditty-tabs--secondary {
  background: #f2f3f5;
  border-bottom: 1px solid #ccc;
}

.ditty-tabs--cloud {
  gap: 5px;
  flex-wrap: wrap;
  height: auto;
  background: #fff;
  color: #1d2327;
}
.ditty-tabs--cloud .ditty-tab {
  width: 70px;
  height: 60px;
  flex: 1 0 auto;
  flex-direction: column;
  align-items: center;
  color: rgba(29, 35, 39, 0.8);
  background: rgba(0, 0, 0, 0.05);
  border-radius: 3px;
  padding: 5px;
}
.ditty-tabs--cloud .ditty-tab:hover:not(.ditty-tab--active) {
  color: #1d2327;
  background: rgba(0, 0, 0, 0.1);
}
.ditty-tabs--cloud .ditty-tab--active {
  background: #19bf7c;
}
.ditty-tabs--cloud .ditty-tab--active .ditty-tab__icon {
  display: block;
  color: #fff;
}
.ditty-tabs--cloud .ditty-tab--active .ditty-tab__label {
  color: #fff;
}
.ditty-tabs--cloud .ditty-tab__icon {
  height: 20px;
  flex: 0 0 auto;
}
.ditty-tabs--cloud .ditty-tab__label {
  display: block;
  font-size: 11px;
  font-weight: 400;
  margin-top: 5px;
  flex: 0 0 auto;
}
.ditty-tabs--cloud .ditty-tab.ditty-preview {
  color: rgba(29, 35, 39, 0.5);
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.ditty-tabs--cloud .ditty-tab.ditty-preview:hover:not(.ditty-tab--active) {
  color: #1d2327;
  background: #fff;
  border-color: rgba(0, 0, 0, 0.2);
}
.ditty-tabs--cloud .ditty-tab.ditty-preview.ditty-tab--active {
  color: #1d2327;
  background: #fff;
  border-color: rgba(0, 0, 0, 0.2);
}
.ditty-tabs--cloud .ditty-tab.ditty-preview.ditty-tab--active .ditty-tab__icon,
.ditty-tabs--cloud .ditty-tab.ditty-preview.ditty-tab--active .ditty-tab__label {
  color: #1d2327;
}

.ditty-tabs--list {
  flex-direction: column;
  align-items: stretch;
  height: auto;
}
.ditty-tabs--list .ditty-tab {
  height: 54px;
