  display: flex;
}
.ditty-field__heading + .ditty-field__input__container {
  margin-top: 10px;
}
.ditty-field__icon {
  font-size: 18px;
  margin-right: 15px;
  margin-top: 2px;
}
.ditty-field__label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  cursor: default;
}
.ditty-field__help-icon {
  color: #ccc;
}
.ditty-field__help-icon.active {
  color: #19bf7c;
}
.ditty-field__help, .ditty-field__description {
  width: 100%;
  font-size: 13px;
  line-height: 16px;
  margin-top: 5px;
