.ditty-field__input {
  flex: 1;
}
.ditty-field__input__container {
  display: flex;
  align-items: center;
  gap: 8px;
}
.ditty-field .components-base-control__field {
  margin: 0;
}
.ditty-field input:not([type=button]):not([type=checkbox]):not([type=radio]),
.ditty-field select,
.ditty-field textarea {
  width: 100%;
