.ditty-field__input--spacing__input,
.ditty-field__input--radius__input {
  width: calc(50% - 5px);
}
.ditty-field__input--spacing .ditty-field--unit,
.ditty-field__input--radius .ditty-field--unit {
  padding: 0;
  border: none;
