.ditty-field__input--spacing__box span:nth-child(1) {
  top: 0;
  left: 3px;
  width: calc(100% - 6px);
  height: 2px;
}
.ditty-field__input--spacing__box span:nth-child(2) {
  bottom: 0;
  left: 3px;
  width: calc(100% - 6px);
  height: 2px;
}
.ditty-field__input--spacing__box span:nth-child(3) {
  top: 3px;
  left: 0;
  height: calc(100% - 6px);
  width: 2px;
}
.ditty-field__input--spacing__box span:nth-child(4) {
  top: 3px;
  right: 0;
  height: calc(100% - 6px);
  width: 2px;
}

.ditty-field__input--radius__box {
  border-radius: 5px;
  overflow: hidden;
}
.ditty-field__input--radius__box:before {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  width: 16px;
  height: 16px;
  background: #fff;
  z-index: 9;
  transform: rotate(45deg);
}
.ditty-field__input--radius__box:after {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 3px;
  z-index: 9;
