.ditty-field__input--radius__box span:nth-child(1) {
  top: 0;
  left: 0;
}
.ditty-field__input--radius__box span:nth-child(2) {
  top: 0;
  right: 0;
}
.ditty-field__input--radius__box span:nth-child(3) {
  bottom: 0;
  left: 0;
}
.ditty-field__input--radius__box span:nth-child(4) {
  bottom: 0;
  right: 0;
}

